import { Db } from "../database/Db_new";

export function splitArray(array, part) {
	var tmp = [];
	for (var i = 0; i < array.length; i += part) {
		tmp.push(array.slice(i, i + part));
	}
	return tmp;
}
/**
 * @param {object} Obj Array of object
 * @param {array} path a array of string
 * @param {any} key key props in Obj
 * @return An object found by path
 * @author luong van tuan
 * @date 21:20 PM - 31/7/2022
 */

export function getObjByPath(Obj, path, key) {
	// path = path.split("/");
	function deep(Obj, path, curr_pos) {
		let i = 0;
		let temp = undefined;
		while (i < Obj.length) {
			if (Obj[i][key] === path[curr_pos]) {
				// nu item hin ti c children v  mng path cha duyt ht th  quy xung tng tip theo ca obj
				if (Obj[i].children?.length && curr_pos !== path.length - 1) {
					return deep(Obj[i].children, path, curr_pos + 1);
				} else {
					temp = Obj[i];
				}
			}
			i++;
		}
		// nu  duyt ti phn t cui cng trong mng path th tr v obj tng ng vi path 
		if (curr_pos === path.length - 1) {
			return temp;
		}
	}
	return deep(Obj, path, 0);
}

/**
 *
 * @param {array} ArrayOfObj
 * @param {string} key
 * @returns Mng cha  gi tr ng vi key
 * @description Hm nhn vo 1 mng i tng c th lng nhau v tr v mt mng cha cc [key] prop bn trong n.
 */
export function getAllValueByKey(ArrayOfObj, key) {
	let result = [];
	for (let i = 0; i < ArrayOfObj.length; i++) {
		result.push(ArrayOfObj[i][key]);
		// nu item c children th thc hin  quy , tr kt qu t di ln.
		if (ArrayOfObj[i].children) {
			result.push(...getAllValueByKey(ArrayOfObj[i].children, key));
		}
	}
	return result;
}
export function shuffleArr(array) {
	let currentIndex = array.length,
		randomIndex;

	// While there remain elements to shuffle.
	while (currentIndex !== 0) {
		// Pick a remaining element.
		randomIndex = Math.floor(Math.random() * currentIndex);
		currentIndex--;

		// And swap it with the current element.
		[array[currentIndex], array[randomIndex]] = [
			array[randomIndex],
			array[currentIndex],
		];
	}

	return array;
}
// thao tac voi DB
export async function GetDatasAt(db_name, key, config) {
	let data = undefined;
	if (!config.key) return Promise.resolve([]);

	if (config.limit) {
		data = await Db[db_name].orderBy(key).limit(config.limit).toArray();
		return Promise.resolve(data);
	} else {
		data = await Db[db_name].where(key).equals(config.key).toArray();
	}
	return Promise.resolve(data);
}
export async function UpdateDataAt(db_name, key, config) {
	await Db[db_name]
		.where(key)
		.equals(config.key)
		.modify((data) => {
			data[config.field] = config.newValue;
		});
}
export async function DeleteAt(db_name, key, delete_key) {
	await Db[db_name].where(key).equals(delete_key).delete();
}
