import { lazy, useRef, Suspense, useEffect, createContext } from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Tippy from "@tippyjs/react/headless";
import useDarkMode from "./hooks/use-dark-mode";
import DarkModeToggle from "react-dark-mode-toggle";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGear } from "@fortawesome/free-solid-svg-icons";
import styles from "./App.module.scss";
import Header from "./components/Header";
import Input from "./components/SetTingMenu/Input";
import Sidebar from "./components/Sidebar";
import createPersistedState from "./hooks/use-persisted-state";
const MainContent = lazy(() => import("./components/MainContent"));
const SettingMenu = lazy(() => import("./components/SetTingMenu"));
export const sidebarContext = createContext();
export const mainContext = createContext();
const icon1 =
	'<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" viewBox="0 0 320 512"><path d="M310.6 361.4c12.5 12.5 12.5 32.75 0 45.25C304.4 412.9 296.2 416 288 416s-16.38-3.125-22.62-9.375L160 301.3L54.63 406.6C48.38 412.9 40.19 416 32 416S15.63 412.9 9.375 406.6c-12.5-12.5-12.5-32.75 0-45.25l105.4-105.4L9.375 150.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 210.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25l-105.4 105.4L310.6 361.4z"/></svg>';
const icon2 =
	'<svg xmlns="http://www.w3.org/2000/svg"  width="25" height="25" fill="currentColor"  viewBox="0 0 448 512"><path d="M0 96C0 78.33 14.33 64 32 64H416C433.7 64 448 78.33 448 96C448 113.7 433.7 128 416 128H32C14.33 128 0 113.7 0 96zM0 256C0 238.3 14.33 224 32 224H416C433.7 224 448 238.3 448 256C448 273.7 433.7 288 416 288H32C14.33 288 0 273.7 0 256zM416 448H32C14.33 448 0 433.7 0 416C0 398.3 14.33 384 32 384H416C433.7 384 448 398.3 448 416C448 433.7 433.7 448 416 448z"/></svg>';

const setingProps = (theme, handleChangeSetting, hanziComponentOptions) => {
	return {
		listItems: [
			{
				lable: "theme",
				item: (
					<DarkModeToggle
						onChange={theme.toggle}
						checked={theme.value}
						size={45}
					/>
				),
			},
			{
				lable: "highlightOnComplete",
				item: (
					<Input
						name='highlightOnComplete'
						type='checkbox'
						defaultChecked={
							hanziComponentOptions.highlightOnComplete
						}
						className={styles["set-input"]}
					/>
				),
			},
			{
				lable: "showOutline",
				item: (
					<Input
						name='showOutline'
						type='checkbox'
						defaultChecked={hanziComponentOptions.showOutline}
						className={styles["set-input"]}
					/>
				),
			},
			{
				lable: "width",
				item: (
					<div>
						{" "}
						<Input
							name='width'
							type='number'
							min={100}
							max={500}
							defaultValue={hanziComponentOptions.width}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>px</span>
					</div>
				),
			},
			{
				lable: "height",
				item: (
					<div>
						{" "}
						<Input
							name='height'
							type='number'
							min={100}
							max={500}
							defaultValue={hanziComponentOptions.height}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>px</span>
					</div>
				),
			},
			{
				lable: "drawingWidth",
				item: (
					<div>
						{" "}
						<Input
							name='drawingWidth'
							type='number'
							min={4}
							max={32}
							defaultValue={hanziComponentOptions.drawingWidth}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>px</span>
					</div>
				),
			},
			{
				lable: "padding",
				item: (
					<div>
						{" "}
						<Input
							name='padding'
							type='number'
							min={10}
							max={50}
							defaultValue={hanziComponentOptions.padding}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>px</span>
					</div>
				),
			},

			{
				lable: "strokeAnimationSpeed",
				item: (
					<div>
						{" "}
						<Input
							name='strokeAnimationSpeed'
							type='number'
							min={0.25}
							max={2}
							defaultValue={
								hanziComponentOptions.strokeAnimationSpeed
							}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>step</span>
					</div>
				),
			},
			{
				lable: "delayBetweenStrokes",
				item: (
					<div>
						{" "}
						<Input
							name='delayBetweenStrokes'
							type='number'
							min={500}
							max={5000}
							defaultValue={
								hanziComponentOptions.delayBetweenStrokes
							}
							className={styles["set-input"]}
						/>
						<span style={{ marginLeft: 5 }}>ms</span>
					</div>
				),
			},
			{
				lable: "outlineColor",
				item: (
					<div>
						{" "}
						<Input
							name='outlineColor'
							type='color'
							defaultValue={hanziComponentOptions.outlineColor}
							className={styles["set-input"]}
						/>
					</div>
				),
			},
			{
				lable: "strokeColor",
				item: (
					<div>
						{" "}
						<Input
							name='strokeColor'
							type='color'
							defaultValue={hanziComponentOptions.strokeColor}
							className={styles["set-input"]}
						/>
					</div>
				),
			},
			{
				lable: "radicalColor",
				item: (
					<div>
						{" "}
						<Input
							name='radicalColor'
							type='color'
							defaultValue={hanziComponentOptions.radicalColor}
							className={styles["set-input"]}
						/>
					</div>
				),
			},
			{
				lable: "drawingColor",
				item: (
					<div>
						{" "}
						<Input
							name='drawingColor'
							type='color'
							defaultValue={hanziComponentOptions.drawingColor}
							className={styles["set-input"]}
						/>
					</div>
				),
			},
			{
				lable: "highlightColor",
				item: (
					<div>
						{" "}
						<Input
							name='highlightColor'
							type='color'
							defaultValue={hanziComponentOptions.highlightColor}
							className={styles["set-input"]}
						/>
					</div>
				),
			},

			{
				lable: "highlightCompleteColor",
				item: (
					<div>
						{" "}
						<Input
							name='highlightCompleteColor'
							type='color'
							defaultValue={
								hanziComponentOptions.highlightCompleteColor
							}
							className={styles["set-input"]}
						/>
					</div>
				),
			},
		],
	};
};
function App() {
	console.log("app render");
	const theme = useDarkMode();
	const ButtonToggleSideBar_Node = useRef();
	const SideBarRef = useRef();
	const AppRef = useRef();
	const SidebarRef = useRef();
	const MainRef = useRef();
	const [hanziComponentOptions, setHanziComponentOptions] =
		createPersistedState("hanziComponentOptions")({
			showOutline: true,
			width: 150,
			height: 150,
			padding: 20,
			strokeAnimationSpeed: 1,
			delayBetweenStrokes: 1000,
			strokeColor: "#555",
			radicalColor: null,
			highlightColor: "#AAF",
			outlineColor: "#DDD",
			drawingColor: "#333",
			drawingWidth: 4,
			highlightOnComplete: true,
			highlightCompleteColor: "#AAF",
			renderer: "svg",
		});
	const handleChangeSetting = (event) => {
		event.preventDefault();
		const props = Array.from(new FormData(event.target).entries()).reduce(
			(memo, [key, v]) => ({
				...memo,
				[key]: v === "on" ? true : v,
			}),
			{}
		);
		if (!props.highlightOnComplete) props.highlightOnComplet = false;
		if (!props.showOutline) props.showOutline = false;
		setHanziComponentOptions(props);
	};
	const toggleSidebar = () => {
		SideBarRef.current.classList.toggle(styles["showSidebar"]);
		let icon = null;
		if (SideBarRef.current.classList.contains(styles["showSidebar"])) {
			if (window.innerWidth < 920) {
				SideBarRef.current.previousSibling.style.display = "none";
			}
			icon = icon1;
		} else {
			SideBarRef.current.previousSibling.style.display = "flex";
			icon = icon2;
		}
		ButtonToggleSideBar_Node.current.innerHTML = icon;
	};

	useEffect(() => {
		AppRef.current = SidebarRef.current;
	}, []);
	return (
		<div className={styles["App-container"]}>
			<Header>
				<button
					ref={ButtonToggleSideBar_Node}
					className={`${styles["Header-item"]} ${styles["onMobile-toggle-sisebar-btn"]}`}
					onClick={() => {
						toggleSidebar();
					}}
				>
					<svg
						xmlns='http://www.w3.org/2000/svg'
						width='25'
						height='25'
						fill='currentColor'
						viewBox='0 0 448 512'
					>
						<path d='M0 96C0 78.33 14.33 64 32 64H416C433.7 64 448 78.33 448 96C448 113.7 433.7 128 416 128H32C14.33 128 0 113.7 0 96zM0 256C0 238.3 14.33 224 32 224H416C433.7 224 448 238.3 448 256C448 273.7 433.7 288 416 288H32C14.33 288 0 273.7 0 256zM416 448H32C14.33 448 0 433.7 0 416C0 398.3 14.33 384 32 384H416C433.7 384 448 398.3 448 416C448 433.7 433.7 448 416 448z' />
					</svg>
				</button>
			</Header>
			<div className={styles["App-main"]}>
				<div className={styles["App-content"]}>
					<mainContext.Provider
						value={{
							hanziComponentOptions,
						}}
					>
						<Suspense
							fallback={
								<div style={{ padding: "16px" }}>
									Loading...
								</div>
							}
						>
							<MainContent
								ref={MainRef}
								hanziComponentOptions={hanziComponentOptions}
								onHistoryChange={(v) => {
									SidebarRef.current?.saveHistory(v);
								}}
							/>
						</Suspense>
					</mainContext.Provider>

					<div ref={SideBarRef} className={styles["App-sidebar"]}>
						<sidebarContext.Provider
							value={{
								onSetScreen: function (v) {
									MainRef.current.setScreen(v);
								},
								setTag: function (v) {
									MainRef.current.setTag(v);
									toggleSidebar();
								},
								onItemClick: function (v) {
									MainRef.current.setArrayOfHanzi(v);
									toggleSidebar();
								},
								onclickSubItem: function (id) {
									MainRef.current.addTab(id);
									toggleSidebar();
								},
							}}
						>
							<Sidebar ref={SidebarRef}>
								<Tippy
									interactive
									hideOnClick={false}
									delay={[0, 500]}
									placement='left-start'
									render={(attrs) => (
										<div
											className='tippy-wrapper'
											tabIndex='-1'
											{...attrs}
										>
											<Suspense
												fallback={<div>Loading...</div>}
											>
												<SettingMenu
													data={setingProps(
														theme,
														() => {},
														hanziComponentOptions
													)}
													Apply={handleChangeSetting}
												/>
											</Suspense>
										</div>
									)}
								>
									<div className={styles["setting-icon"]}>
										<FontAwesomeIcon icon={faGear} />
									</div>
								</Tippy>
							</Sidebar>
						</sidebarContext.Provider>
					</div>
				</div>
			</div>
			<ToastContainer
				theme='dark'
				position='bottom-right'
				autoClose={3000}
				hideProgressBar={true}
			/>
		</div>
	);
}

export default App;
